html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 0.875rem;
  font-family: "Outfit", sans-serif;
  background-color: var(--light);
}

main {
  padding: 2em;
  min-height: 100vh;
}

:root {
  /* color */
  --white: hsl(0, 0%, 100%);
  --light-gray: hsl(0, 0%, 100%);
  --grayish-blue: hsl(220, 15%, 55%);
  --dark-blue: hsl(218, 44%, 22%);
  --light: #d5e1ef;
}

h2 {
  font-size: 1.25rem;
  margin-bottom: 0.9375rem;
  font-weight: 700;
}

.grid {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  padding: 0 1.5rem;
  margin: auto;
}
@media (min-width: 71.875em) {
  .container {
    max-width: 69.375rem;
    padding: 0;
  }
}

.h-full {
  min-height: 100vh;
}

.card {
  background-color: var(--white);
  box-shadow: 0 0.125rem 1.25rem 0 rgba(0, 0, 0, 0.1);
  padding: 0.8em;
  max-width: 21.875rem;
  text-align: center;
  border-radius: 1.25rem;
}
.card__header img {
  max-width: 100%;
  width: 15.625rem;
  border-radius: 0.625rem;
}
.card__body {
  padding: 1rem 0.2em;
}
.card__title {
  color: var(--dark-blue);
  text-align: center;
}
.card__desc {
  color: var(--grayish-blue);
  text-align: center;
}

.footer {
  text-align: center;
}
.footer__attribution {
  padding: 0.625rem;
  background: var(--dark-blue);
  border-top-right-radius: 2.5rem;
  border-top-left-radius: 2.5rem;
  color: var(--white);
}
.footer__attribution a {
  color: var(--dark-cyan);
  transition: color 350ms ease;
}
.footer__attribution a:hover {
  color: #1fcbdb;
}
.footer__attribution a:focus, .footer__attribution a:active {
  color: var(--dark-cyan);
}